@import url(https://unpkg.com/leaflet@1.0.1/dist/leaflet.css);
@import url(https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.leaflet {
  height: 100vh;
  width: 100%;
}
.leaflet-container {
  height: 100vh;
}
.react-search-box-dropdown-list-item {
  color: black;
}
.leaflet-control-container * {
  z-index: 400;
}
/*
li.sc-fzoxKX:nth-child(1) a,
li.sc-fzomME:nth-child(2) a {
  background-color: #3f51b5;
  color: white;
  border-radius: 2px;
}
*/
.leaflet-div-icon {
  cursor: pointer;
  background: transparent;
  border: none;
}
.sc-fznyYp,
.sc-fzomME {
  border: 1px solid white !important;
  font-family: "Exo 2", "Calibri", sans-serif;
}
.react-search-box-dropdown-list-item {
  font-family: "Exo 2", "Calibri", sans-serif;
}
.leaflet-tooltip-pane {
  font-family: "Exo 2", "Calibri", sans-serif;
}
.niebieski {
  color: #3f51b5;
}
.leaflet-control-zoom:nth-child(1) {
  display: none;
}
.leaflet-left {
  right: 24px;
  bottom: 0;
}
.leaflet-control {
  float: right;
}
.bm-burger-button {
  top: 24px !important;
}
.zNoeh {
  margin: 0 0 0 0 !important;
}
input{
  font-family: "Exo 2", "Calibri", sans-serif;
  border:none !important;
height: 48px !important;
}
.leaflet-control-scale {
  float: left;
}
.react-resizable {
  max-width: 100%;
}
.bm-item-list a{
  text-align:center;
  line-height:2.1;

}
.react-resizable-handle {
  bottom: -10px;
  right: -10px;
}

.lagRadar {
  position: fixed;
  bottom: 0.5rem;
  right: 0.5rem;
}
.dupa {
  width: 200px;
  height: 200px;
  float: right;
}
.leaflet-top .leaflet-control {
  position: absolute;
  bottom: 91px;
  right: 0;
}
.leaflet-control-scale {
  float: right;
}
.leaflet-bottom .leaflet-control-scale {
  margin-bottom: 32px;
}
.leaflet-control-scale {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.leaflet-control-scale * {
  text-align: right;
}
.bm-overlay{
  background-color: #3f51b5 !important;
}


  .hideMobile{
    display:block;
  }
@media (max-width: 800px) {

.hideMobile{
  display:none;
}
  .leaflet-control-zoom {
    display: none;
  }
}

* {
  padding: 0;
  margin: 0;
  outline: none;
}
ul {
  list-style-type: none;
}
