@import url("https://unpkg.com/leaflet@1.0.1/dist/leaflet.css");
.leaflet {
  height: 100vh;
  width: 100%;
}
.leaflet-container {
  height: 100vh;
}
.react-search-box-dropdown-list-item {
  color: black;
}
.leaflet-control-container * {
  z-index: 400;
}
