.leaflet-div-icon {
  cursor: pointer;
  background: transparent;
  border: none;
}
.sc-fznyYp,
.sc-fzomME {
  border: 1px solid white !important;
  font-family: "Exo 2", "Calibri", sans-serif;
}
.react-search-box-dropdown-list-item {
  font-family: "Exo 2", "Calibri", sans-serif;
}
.leaflet-tooltip-pane {
  font-family: "Exo 2", "Calibri", sans-serif;
}
.niebieski {
  color: #3f51b5;
}
.leaflet-control-zoom:nth-child(1) {
  display: none;
}
.leaflet-left {
  right: 24px;
  bottom: 0;
}
.leaflet-control {
  float: right;
}
.bm-burger-button {
  top: 24px !important;
}
.zNoeh {
  margin: 0 0 0 0 !important;
}
input{
  font-family: "Exo 2", "Calibri", sans-serif;
  border:none !important;
height: 48px !important;
}
.leaflet-control-scale {
  float: left;
}
.react-resizable {
  max-width: 100%;
}
.bm-item-list a{
  text-align:center;
  line-height:2.1;

}
.react-resizable-handle {
  bottom: -10px;
  right: -10px;
}

.lagRadar {
  position: fixed;
  bottom: 0.5rem;
  right: 0.5rem;
}
.dupa {
  width: 200px;
  height: 200px;
  float: right;
}
.leaflet-top .leaflet-control {
  position: absolute;
  bottom: 91px;
  right: 0;
}
.leaflet-control-scale {
  float: right;
}
.leaflet-bottom .leaflet-control-scale {
  margin-bottom: 32px;
}
.leaflet-control-scale {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.leaflet-control-scale * {
  text-align: right;
}
.bm-overlay{
  background-color: #3f51b5 !important;
}


  .hideMobile{
    display:block;
  }
@media (max-width: 800px) {

.hideMobile{
  display:none;
}
  .leaflet-control-zoom {
    display: none;
  }
}
